

































@import "@/scss/theme";

.duty-contents {
  display: flex;
  position: relative;
  .border-block {
    flex: 1;
    position: static;
    &::before {
      content: ' ';
      width: 9px;
      height: 9px;
      border-radius: 50%;
      position: absolute;
      display: inline-block;
      background-color: #a8a8a8;
      margin-top: 10px;
      margin-left: .5px;
      z-index: 1;
    }
    &::after {
      content: '';
      position: absolute;
      width: 1px;
      height: 100%;
      background-color: #979797;
      display: block;
      margin-left: 4.5px;
      top: 10px;
      z-index: 0;
    }
  }
  .border-block-end {
    flex: 1;
    position: static;
    &::before {
      content: ' ';
      width: 9px;
      height: 9px;
      border-radius: 50%;
      position: absolute;
      display: inline-block;
      background-color: #a8a8a8;
      margin-top: 10px;
      margin-left: .5px;
      z-index: 1;
    }
  }
  .work-project {
    flex: 9;
    h3 {
      color: $color-header-menu;
      margin-top: 0;
    }
    a {
      text-decoration: none;
      color: #0bbfc5;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
